import React, { useEffect, useMemo, useState } from 'react'
import { styled } from '@mui/system';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';
import { useDebug } from '../../../hooks/basic/useDebug';

const StyledTable = styled(Table)(({ theme }) => ({
  width: '100%',
  // '& .MuiPaper-root.MuiTableContainer-root': {
  //   borderRadius: '10px',
  //   overflow: 'hidden',
  // },
  '& .MuiTableCell-root': {
    padding: '5px 0',
    fontSize: '14px',
    width: '102.86px',
  },
  '& .MuiTableCell-head': {
    fontSize: '16px',
    fontWeight: theme.font.weight.bold,
    color: '#ffffff',
    backgroundColor: '#555555',
    textAlign: 'center',
    border: `1px solid ${theme.palette.table.border}`,
    borderRight: '1px solid #ffffff',
    '&:last-child': {
      borderRight: `1px solid ${theme.palette.table.border}`,
    },
  },
  '& .MuiTableCell-body': {
    height: '70px',
    fontSize: theme.font.size.sm,
    color: theme.palette.table.text,
    textAlign: 'center',
    border: `1px solid ${theme.palette.table.border}`,
  },
}));


const ClassSchedule = ({ data, setData, edit, classId }) => {

  const defaultChecks = {
    mon_time: !!data?.mon_time,
    tue_time: !!data?.tue_time,
    wed_time: !!data?.wed_time,
    thu_time: !!data?.thu_time,
    fri_time: !!data?.fri_time,
    sat_time: !!data?.sat_time,
    sun_time: !!data?.sun_time,
  }
  const [checkList, setCheckList] = useState(defaultChecks)


  useEffect(() => {
    setCheckList(defaultChecks)
  }, [edit])
  
  // handler
  const handleChange = (e) => {
    const key = e.target.parentElement.id
    const start = e.target.parentElement.children[0].value
    const end = e.target.parentElement.children[1].value
    let val = `${start}~${end}`
    setData({...data, [key]:val})
  }

  const handleCheck = (e) => {
    const key = e.target.name
    setCheckList({...checkList, [key]: e.target.checked})
    
    if (!e.target.checked) {
      setData({...data, [key]:''})
    }
  }

  const _list = useMemo(() => {
    return [
    { day: '월', key:'mon_time'},
    { day: '화', key:'tue_time'},
    { day: '수', key:'wed_time'},
    { day: '목', key:'thu_time'},
    { day: '금', key:'fri_time'},
    { day: '토', key:'sat_time'},
    { day: '일', key:'sun_time'},
  ]

},[])

  return (
    <>
      <TableContainer component={Paper}>
        <StyledTable>
          <TableHead>
            <TableRow>
              { React.Children.toArray(
                _list.map( item =>
                  <TableCell>
                    { edit && 
                      <input 
                        name={`${item.key}`}
                        type='checkbox'
                        onChange={handleCheck} 
                        checked={checkList[item.key]}
                      /> 
                    }
                    {item.day}
                  </TableCell>
                )
              )}
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow>
              { React.Children.toArray(
                _list.map( item => 
                  <TableCell>
                    { !edit && 
                    <div className="flex flex-col tracking-normal text-[15px]" id={`${item.key}`}>
                      <span>{data[item.key]?.split('~')[0] || ''}</span>
                      <span>{data[item.key]? `~${data[item.key]?.split('~')[1]}` : ''}</span>
                    </div>
                    }
                    { edit &&
                    <div className="flex flex-col" id={`${item.key}`}>
                      <input 
                        type='time'
                        value={data[item.key]?.split('~')[0] || ''} 
                        onChange={handleChange} 
                        disabled={!checkList[item.key]}
                        className='w-[102px] m-auto mb-[2px]'
                        />
                      <input 
                        type='time'
                        value={data[item.key]?.split('~')[1] || ''} 
                        onChange={handleChange} 
                        disabled={!checkList[item.key]}
                        className='w-[102px] m-auto'
                        />
                    </div>
                    }
                  </TableCell>
                )
              )}
            </TableRow>
          </TableBody>
        </StyledTable>
      </TableContainer>
    </>
  )
}

export default ClassSchedule